<template>
    <div class="bgColor">
        <div class="header">
            <img src="../../../images/creatAdminAccount.png" alt="">
            <div class="curatorText">
                <img src="../../../images/curatorText02.png" alt="">
            </div>
        </div>
        <div class="content">
            <div class="inject">
                <p>1300000000</p>
            </div>
            <p class="button-list">
                <span class="rechange" @click="reChange">更换</span>
                <span class="insure" @click="tobeCurator">确定</span>
            </p>

        </div>
        <footer-text></footer-text>

    </div>

</template>

<script>
import footerText from '../../../components/footerText'
export default {
    components: {
        footerText,
  },
  data() {
      return {

      }
  },
  methods:{
      reChange:function(){
          // this.$router.push({path:'/becomeCurator/bindPhone'})
          //返回后台数据方法待添加
      },
      tobeCurator:function(){
           this.$router.push({path:'/becomeCurator/Apply'})
      }
  }
}
</script>

<style lang="less" scoped>
    .bgColor{
        height:100%;
        min-height:16rem;
        background:#EEEEEE;
        position: relative;
        #footer_text{
            position: absolute;
            bottom:0;
        }
    }
    .header{
        height: 5rem;
        background: #4CACFF;
        text-align: center;
        position: relative;
        img{
            width:84%;
            margin-top:14%;
        }
        .curatorText{
            position: absolute;
            width:88%;
            bottom:-24%;
            left:6%;
            margin:0 auto;
            border-radius: 10px;
            background:#fff;
            text-align: center;
            img{
                width:98%;
                margin: 4% 0;
            }
        }
    }
    .content{
        text-align: center;
        margin-top: 20%;
        .inject{
            width: 80%;
            // height:2.5em;
            border:1px solid #CBCBCB;
            border-radius: 6px;
            margin: 0 auto;
            margin-bottom:10%;
            background: #fff;
            p{
                height:100%;
                color:#666;
                line-height: 1.4rem;
                font-size:.6rem;
            }
        }
        .button-list{
             width:90%;
             margin: 0 auto;
        }
         .rechange, .insure{
             width:45%;
             height:1.4rem;
             color:#fff;
             font-size:.5rem;
             line-height: 1.4rem;
             margin: 0 1%;
             display: inline-block;
             background-color: #EE8F45;
             border-radius: 6px;
        }
        .insure{
            background: #5BA9ED;
        }

    }
</style>



